body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: rgb(237, 201, 213); */

}

body::before{
  content: "";
  position: fixed;
  inset: 0;
  z-index: -1;
  pointer-events: none;
  /* background-image: url(https://as2.ftcdn.net/v2/jpg/02/59/78/95/1000_F_259789521_uWCeEJXj41HWbyQNDc6pDywAIlkNNbY7.jpg) ; */
  /* background-image: url("https://phunugioi.com/wp-content/uploads/2021/11/Background-banner-cac-khoi-hinh.jpg"); */
  /* backdrop-filter: blur(15px); */
  /* background-image: url("https://free.vector6.com/wp-content/uploads/2020/07/Vector-Hinh-Khoi-Nghe-Thuat-10.jpg"); */
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgb(0, 0, 0);
  opacity: 0.3;
  background-size: cover;
  /* animation: backgroundZooomAnimate 20s forwards; */
  /* animation: backgroundZooomAnimate1 20s forwards; */

  
  /* lặp lại animation vô tận  */
  animation-iteration-count: infinite;
}

@keyframes backgroundZooomAnimate {
  from {
      transform: scale(1);
  }
  to {
      transform: scale(1.3);
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

